.employees {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
}

.table {
    padding: 15px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    background-color: #fff;
    border-radius: 5px;
}
.antTable{
    width:100%;
    background-color: #fff;
    border-radius: 5px;
}
.steps{
    background-color: #fff;
    border-radius: 5px;
    width: 100%;
    padding: 15px;
}

.employees_steps_content {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    padding: 20px;
}

.tableBtns {
    display: flex;
    justify-content: start;
    align-items: center;
    width: 100%;
}

.tableBtns button {
    margin-right: 10px;
}