.allAttendance {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;

}
.attendance{
    background-color: #fff;
    border-radius: 5px;
    width: 100%;
    transition: all 0.3s ease-in-out;
}


.table {
    padding-top: 20px;
    width: 100%;
    transition: all 0.3s ease-in-out;
}
.formTable{
    padding: 20px;
    width: 100%;
    transition: all 0.3s ease-in-out;
}


.antTable {
    width: 100%;
    background-color: #fff;
    border-radius: 5px;
    padding: 15px;
}

.closed {
    padding: 10px 15px;
    opacity: 0;
    height: 0;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    transition: all 0.5s ease-in-out;
}
.invalidAttendance {
    width: 100%;
    background-color: #fff;
    border-radius: 5px;
    padding: 15px;
    display: flex;
    align-items: start;
}
.employeeList{
    width:35%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
}
.all_employees{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 15px;
    background-color: #fff;
    border-radius: 5px;
}
.exceptions_form{
    width:60%
}
.employee_desc p{
    margin: 0;
    padding: 0;
    font-weight: 600;
    font-size: 1.1rem;
}
.employee_desc span{
    font-weight: 400;
    color: #999;
    font-size: 0.9rem;
}

