.salary_container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
}

.departmentsTitle {
    padding-bottom: 0;
    margin: 0;
}

.departmentsDesc {
    font-size: 0.9rem;
    color: #6c757d;
}

.departments_cards, .departments_cards_1, .departments_cards_2, .departments_cards_3, .departments_cards_4 {
    display: grid;
    justify-content: space-around;
    align-items: center;
    position: relative;
}

.departments_cards {
    width: 100%;
}

.departments_cards_1 {
    grid-template-columns: 1fr;
}

.departments_cards_2 {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    width: 100%;
}

.departments_cards_3 {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    width: 100%;
}

.card {
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    height: 12rem;
    padding: 15px;
    transition: all 0.2s ease-in-out;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.card h3 {
    border-left: #3675b4 5px solid;
    padding-left: 8px;
}

.card:hover {
    transform: translateY(-5px);
}

.card_btns {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}

.card_btns button, .form_btns button {
    margin-right: 10px;
}

.form_btns {
    display: flex;
    flex-direction: row;
    justify-content: end;
    align-items: center;
}

.no_data {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    font-size: 1.5rem;
    color: #6c757d;
}

.viewDetails {
    width: 100%;
}

.employee_card, .deduction_card, .rewards_card, .overTimes_card, .otherDeductions_card {
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    padding: 15px;
    transition: all 0.2s ease-in-out;
}

