.App {
  height: 100vh;
}

.side-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 3rem;
  padding: 10px;
}


.logo {
  background-image: url('./Assets/imgs/egyCodes.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  height: 5.5rem;
  width: 40%;
}

.logo-closed {
  display: none;
}

.ant-layout-sider {
  text-align: center;
}

.site-layout-background-header {
  background: #fff !important;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.site-layout-background-header-without-padding {
  background: #fff !important;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px !important;
}

.site-layout-background-content {
  background: #f5f5f5 !important;
  display: flex;
  border-radius: 5px;
  justify-content: flex-start;
  align-items: start;
  height: 100%;
  overflow-y: auto;
}

.ant-layout-sider-trigger {
  top: 0;
}

.ant-menu-item {
  height: 3rem !important;
  margin: 30px 0 !important;
}

.ant-menu-item-selected {
  display: flex !important;
}

.ant-btn-primary {
  background-color: #001529 !important;
  color: #eee !important;
}

.ant-btn-primary:hover {
  background-color: #062646 !important;

}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 160px);
  width: 100%;
  z-index: 1000;
}

table {
  width: 100% !important;
  border-collapse: collapse;
}

.horizontal-line {
  border-bottom: 1px solid #eee;
  margin: 0 0 15px 0;
}

.lang {
  display: inline-block;
  width: 100%;
  text-align: end;
  z-index: 1000;
}

.lang-item {
  margin: 0 5px;
  color: #e0e0e0;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.active {
  color: #91bcef;
  font-size: 15px;
  font-weight: bold;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}

.show-logo {
  width: 100px;
  padding: 10px;
  object-fit: contain;
  transition: all 0.3s ease-in-out;
}
.hide-logo{
  visibility: hidden;
  width: 0;
  object-fit: contain;
  transition: all 0.3s ease-in-out;
}

.text-with-side-border {
  padding: 0 10px;
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 20px;
}

.left {
  border-left: #062646 5px solid;
}

.right {
  border-right: #062646 5px solid;
}

.card {
  width: 100%;
  border-radius: 8px;
  background-color: #fff;
  padding: 20px;
  height: 20rem;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 30px;
}
.table-btns a{
  margin-left: 10px;
}


