.home {
    width: 100%;
}

.greatings_with_holidays {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    margin-top: 20px;
}

.upcoming, .greatings {
    width: 45%;
}

.greatings {
    height: 20rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    background-color: #fff;
    border-radius: 8px;
    position: relative;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}

.upcoming a {
    color: #062646;
    font-size: 15px;
    font-weight: 500;
    opacity: 0;
    padding: 5px;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;
}

.upcoming:hover a {
    opacity: 1;
}

.upcoming a:hover {
    background-color: #91bcef;
    color: #fff;
}

.greatings_card {
    width: 100%;
    height: 100%;
    padding: 20px;
    margin-bottom: 20px;
}

.greatings img {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 80%;
    object-fit: cover;
    border-radius: 5px;
}